h1 {
  text-align: center;
}

.circle {
  height: 48px;
  width: 48px;
  border-radius: 24px;
}

.red {
  background-color: red;
}

.blue {
  background-color: blue;
}

.white {
  background-color: white;
}

.circleRow {
  display: flex;
  flex-basis: 400px;
  justify-content: space-between;
  height: 100px;
}